'use client'
import { useState, useRef, useEffect } from 'react';

import { motion } from 'framer-motion';
import { Button } from '@/registry/new-york-v4/ui/button';
import { Avatar } from '@/registry/new-york-v4/ui/avatar';
import { Card, CardContent } from '@/registry/new-york-v4/ui/card';
import { ScrollArea } from '@/registry/new-york-v4/ui/scroll-area';
import { Input } from '@/registry/new-york-v4/ui/input';

const ChatMessage = ({ role, message }: { role: 'user' | 'assistant'; message: string }) => (
  <motion.div
    initial={{ opacity: 0, y: 10 }}
    animate={{ opacity: 1, y: 0 }}
    className={`flex gap-2 my-2 ${role === 'user' ? 'justify-end' : 'justify-start'}`}
  >
    {role === 'assistant' && <Avatar className="h-8 w-8">ðŸ¤–</Avatar>}
    <Card className="max-w-[80%]">
      <CardContent className="p-3 text-sm whitespace-pre-wrap">
        {message}
      </CardContent>
    </Card>
    {role === 'user' && <Avatar className="h-8 w-8">ðŸ§‘</Avatar>}
  </motion.div>
);

export default function ChatInterface() {
  const [messages, setMessages] = useState([
    { role: 'assistant', message: 'What are you working on?' },``
  ]);
  const [input, setInput] = useState('');
  const bottomRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    bottomRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const handleSend = async () => {
    if (!input.trim()) return;
    const newMessages = [...messages, { role: 'user', message: input }];
    setMessages(newMessages);
    setInput('');

    // Simulate bot response (replace with your API call)
    setTimeout(() => {
      setMessages((prev) => [
        ...prev,
        { role: 'assistant', message: `You said: ${input}` },
      ]);
    }, 500);
  };

  const handleKeyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') handleSend();
  };

  return (
    <div className="flex flex-col h-screen bg-background text-foreground">
      <ScrollArea className="flex-1 p-4 overflow-y-auto">
        {messages.map((msg, i) => (
          <ChatMessage key={i} role={msg.role as 'user' | 'assistant'} message={msg.message} />
        ))}
        <div ref={bottomRef} />
      </ScrollArea>

      <div className="p-4 border-t bg-background">
        <div className="flex items-center gap-2">
          <Input
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={handleKeyPress}
            placeholder="Ask anything"
            className="flex-1"
          />
          <Button onClick={handleSend}>Send</Button>
        </div>
      </div>
    </div>
  );
}
